import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { map } from 'rxjs/operators';
import { Observable, of as observableOf, merge } from 'rxjs';

// TODO: Replace this with your own data model type
export interface VehicleTableItem {
  id: number;
  oem: string;
  model: string;
  batteryRating: number;
  slowCharge: number;
  cost: number;
}

// TODO: replace this with real data from your application
const EXAMPLE_DATA: VehicleTableItem[] = [
  { id: 1, oem: 'Hyundai', model: 'Kona', batteryRating: 39.2, slowCharge: 6.17, cost: 2400},
  { id: 2, oem: 'Tata', model: 'Nexon', batteryRating: 30.2, slowCharge: 8.5, cost: 1701},
  { id: 3, oem: 'Mahindra', model: 'E Verito', batteryRating: 21.2, slowCharge: 11.5, cost: 912},
  { id: 4, oem: 'MG', model: 'ZS', batteryRating: 44.5, slowCharge: 6.5, cost: 2358},
  { id: 5, oem: 'Mercedes', model: 'EQC', batteryRating: 80, slowCharge: 11, cost: 9930},
];

/**
 * Data source for the VehicleTable view. This class should
 * encapsulate all logic for fetching and manipulating the displayed data
 * (including sorting, pagination, and filtering).
 */
export class VehicleTableDataSource extends MatTableDataSource<VehicleTableItem> {
  data: VehicleTableItem[] = EXAMPLE_DATA;
  paginator: MatPaginator;
  sort: MatSort;

  constructor() {
    super();
  }

  /**
   * Connect this data source to the table. The table will only update when
   * the returned stream emits new items.
   * @returns A stream of the items to be rendered.
   */
  // connect(): Observable<VehicleTableItem[]> {
  //   // Combine everything that affects the rendered data into one update
  //   // stream for the data-table to consume.
  //   const dataMutations = [
  //     observableOf(this.data),
  //     this.paginator.page,
  //     this.sort.sortChange
  //   ];

  //   return merge(...dataMutations).pipe(map(() => {
  //     return this.getPagedData(this.getSortedData([...this.data]));
  //   }));
  // }

  /**
   *  Called when the table is being destroyed. Use this function, to clean up
   * any open connections or free any held resources that were set up during connect.
   */
  disconnect() {}

  /**
   * Paginate the data (client-side). If you're using server-side pagination,
   * this would be replaced by requesting the appropriate data from the server.
   */
  private getPagedData(data: VehicleTableItem[]) {
    const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
    return data.splice(startIndex, this.paginator.pageSize);
  }

  /**
   * Sort the data (client-side). If you're using server-side sorting,
   * this would be replaced by requesting the appropriate data from the server.
   */
  private getSortedData(data: VehicleTableItem[]) {
    if (!this.sort.active || this.sort.direction === '') {
      return data;
    }

    return data.sort((a, b) => {
      const isAsc = this.sort.direction === 'asc';
      switch (this.sort.active) {        
        case 'id': return compare(+a.id, +b.id, isAsc);
        case 'oem': return compare(a.oem, b.oem, isAsc);
        case 'model': return compare(a.model, b.model, isAsc);
        case 'batteryRating': return compare(a.batteryRating, b.batteryRating, isAsc);
        case 'slowCharge': return compare(a.slowCharge, b.slowCharge, isAsc);
        case 'cost': return compare(a.cost, b.cost, isAsc);
        default: return 0;
      }
    });
  }
}

/** Simple sort comparator for example ID/Name columns (for client-side sorting). */
function compare(a, b, isAsc) {
  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}
